<template>
  <div class="user">
    <panel-container>
      <template #head>
        <div>
          <!-- <router-link to="/">/主页</router-link>
          <img :src="userDetail.author" alt="" />
          <span class="username"> {{ userDetail.author.loginname }}</span> -->
        </div>
      </template>
      <template #content> </template>
    </panel-container>
    <panel-container>
      <template #head>
        <span>/主页</span>
      </template>
      <template #content> </template>
    </panel-container>
    <panel-container>
      <template #head>
        <span>/主页</span>
      </template>
      <template #content> </template>
    </panel-container>
  </div>
</template>

<script>
import PanelContainer from "@/components/PanelContainer.vue";
import { loginUser } from "../http/api";
export default {
  components: { PanelContainer },
  props: ["loginname"],
  data() {
    return {
      userDetail: null,
    };
  },
  watch: {
    loginname: {
      async handler(value) {
        console.log(value);
        const res = await loginUser({ loginname: value });
        this.userDetail = res.data;
      },
    },
    immediate: true,
  },
};
</script>

