import Vue from 'vue'
import App from './App.vue'
import './assets/reset.less'
import './plugins/element'
import router from './router'
import store from './store'
Vue.config.productionTip = false

new Vue({
  render: h => h(App),
  router,
  store
}).$mount('#app')


// 如何将vue 项目运行到外网服务器 （github)

// vue 的部署
// 1. 修改路由模式为hash （现阶段的服务器都是别人的如果使用history的话会出问题)
    // 因为服务器访问一个地址默认是找当前地址的html 只有首页有index.html 所以其他页面刷新会404
    // 需要对服务器进行配置,将所有的地址重新指向根目录下 index.html
    // 比如地址是 http://locallhost:3000/topic

// 2. 当你的项目部署到二级地址的时候（不是根目录）需要配置 publicPath (在vue config.js) 值是二级地址
// 
// 3. 打包编译 html css js (要保证项目在本地是没有报错的)
//    npm run build   serve .

